'use client';

import { useState } from 'react';

export default function ChatbotPage() {
  const [messages, setMessages] = useState([
    { role: 'assistant', content: 'üëã Bonjour ! Je suis ton assistant IA.' }
  ]);
  const [input, setInput] = useState('');
  const [loading, setLoading] = useState(false);

  const sendMessage = async () => {
    if (!input.trim() || loading) return;

    const userMsg = { role: 'user', content: input };
    const updated = [...messages, userMsg];
    setMessages(updated);
    setInput('');
    setLoading(true);

    try {
      // Appelle l'API √† /api/chat
      const res = await fetch('/api/chat', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ messages: updated }),
      });

      const data = await res.json();
      
      if (data.error) throw new Error(data.error);
      
      setMessages([...updated, { role: 'assistant', content: data.content }]);
    } catch (error: any) {
      setMessages([...updated, { 
        role: 'assistant', 
        content: `‚ùå Erreur: ${error.message}` 
      }]);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ padding: 20, maxWidth: 800, margin: '0 auto' }}>
      <h1>ü§ñ Chatbot IA P√©dagogique</h1>
      
      <div style={{ margin: '20px 0' }}>
        {messages.map((msg, i) => (
          <div
            key={i}
            style={{
              padding: 15,
              marginBottom: 10,
              background: msg.role === 'user' ? '#FFFFFF' : '#040404ff',
              borderRadius: 10,
            }}
          >
            <strong>{msg.role === 'user' ? 'üë§ Vous' : 'ü§ñ IA'}:</strong>
            <p style={{ marginTop: 5 }}>{msg.content}</p>
          </div>
        ))}
      </div>

      <div style={{ display: 'flex', gap: 10 }}>
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => e.key === 'Enter' && sendMessage()}
          placeholder="Pose une question sur l'IA..."
          disabled={loading}
          style={{ flex: 1, padding: 10, fontSize: 16 }}
        />
        <button
          onClick={sendMessage}
          disabled={loading}
          style={{ 
            padding: '10px 20px', 
            background: loading ? '#ccc' : '#0070f3',
            color: 'white',
            border: 'none',
            borderRadius: 5,
            cursor: loading ? 'not-allowed' : 'pointer',
          }}
        >
          {loading ? '...' : 'Envoyer'}
        </button>
      </div>
    </div>
  );
}